import React, { useState, useEffect } from 'react';
import { getFirestore, collection, query, where, orderBy, limit, onSnapshot, Timestamp, getDocs } from 'firebase/firestore';
import { LogActionData, ActionDefinition } from './types'; // Import ActionDefinition
// Import ActivityEvent and mapLogToAction if needed for formatting
// import { ActivityEvent, mapLogToAction } from './ItemDetailModal'; 

interface UserActivityViewProps {
  orgId: string;
  onClose: () => void;
  onEntityClick: (itemId: string) => void;
  onActionSelected: (itemId: string, action: ActionDefinition) => void; // Update action type
}

// Helper to get a readable description of the action
const getActionDescription = (log: LogActionData): string => {
  switch (log.action) {
    case 'item.create':
      return 'Item created';
    case 'item.update.fields': {
      if (log.before && log.after) {
        const fields = (log.details?.changedFields as string[] | undefined)
          || Object.keys(log.after).filter(k => JSON.stringify(log.before?.[k]) !== JSON.stringify(log.after?.[k]));
        if (fields.length) {
          const changes = fields.map(f => `${f}: ${log.before?.[f]}  ${log.after?.[f]}`);
          return `Edited ${changes.join(', ')}`;
        }
      }
      const simple = (log.details?.changedFields as string[] | undefined)?.join(', ');
      return simple ? `Edited fields: ${simple}` : 'Item edited';
    }
    default:
      if (log.action.startsWith('item.update.status.')) {
        const oldStat = log.details?.previousStatus || (log.before?.currentStatus as string) || 'Unknown';
        const newStat = log.details?.newStatus || log.action.replace('item.update.status.', '');
        return `Status changed: ${oldStat}  ${newStat}`;
      }
      if (log.action === 'item.update.flag') {
        const flagged = log.details?.isFlagged;
        return flagged ? 'Item flagged' : 'Item unflagged';
      }
      if (log.action === 'item.update.issue.report') {
        return `Issue reported: ${log.details?.description || log.details?.issueDescription || ''}`;
      }
      if (log.action === 'item.update.issue.update') {
        return `Issue update added: ${log.details?.note || ''}`;
      }
      if (log.action === 'item.update.issue.resolve') {
        return `Issue resolved: ${log.details?.resolutionOutcome || ''}`;
      }
      if (log.action === 'item.add_note') {
        return `Note added: ${log.details?.note || ''}`;
      }
      return log.action;
  }
};

const UserActivityView: React.FC<UserActivityViewProps> = ({ orgId, onClose, onEntityClick, onActionSelected }) => {
    const [logs, setLogs] = useState<LogActionData[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);
    // Map of userId to email from org members
    const [members, setMembers] = useState<Record<string, string>>({});

    // Fetch organization members to map userId to email
    useEffect(() => {
        if (!orgId) return;
        const db = getFirestore();
        const membersRef = collection(db, `orgs/${orgId}/members`);
        getDocs(membersRef)
            .then(snapshot => {
                const map: Record<string, string> = {};
                snapshot.docs.forEach(doc => {
                    const data: any = doc.data();
                    map[doc.id] = data.email || doc.id;
                });
                setMembers(map);
            })
            .catch(err => console.error('Error fetching members:', err));
    }, [orgId, setMembers]);

    useEffect(() => {
        if (!orgId) {
            setError('Organization ID is required.');
            setLoading(false);
            return;
        }

        setLoading(true);
        setError(null);
        const db = getFirestore();
        const logsRef = collection(db, `orgs/${orgId}/logs`);
        // Filter to only this tool and order by server timestamp 'ts'
        const q = query(
            logsRef,
            where('tool', '==', 'incomingtool'),
            orderBy('ts', 'desc'),
            limit(100)
        );

        const unsubscribe = onSnapshot(q, (snapshot) => {
            // Map raw Firestore fields to LogActionData
            const fetchedLogs = snapshot.docs.map(doc => {
                const d = doc.data() as any;
                return {
                    orgId,
                    toolKey: d.toolKey || d.tool,
                    action: d.action,
                    entityPath: d.entityPath,
                    timestamp: d.timestamp || d.ts,
                    userId: d.userId || d.uid,
                    userEmail: d.email, // Map Firestore 'email' field
                    before: d.before,
                    after: d.after,
                    details: d.details,
                } as LogActionData;
            });
            // Exclude app-loaded events
            const relevantLogs = fetchedLogs.filter(log => log.action !== 'app-loaded');
            setLogs(relevantLogs);
            setLoading(false);
        }, (err) => {
            console.error("Error fetching user activity logs: ", err);
            setError(`Failed to load activity logs: ${err.message}`);
            setLoading(false);
        });

        return () => unsubscribe();
    }, [orgId, setLogs]);

     // Render a log entry using member emails if available
     const renderLogEntry = (log: LogActionData): React.ReactNode => {
        const time =
            log.timestamp instanceof Timestamp
                ? log.timestamp.toDate().toLocaleString()
                : typeof log.timestamp === 'string'
                ? new Date(log.timestamp).toLocaleString()
                : 'Invalid Date';
        const user = log.userEmail || members[log.userId || ''] || log.userId || 'system';
        const actionDesc = getActionDescription(log);
        const parts = log.entityPath.split('/');
        const entityId = parts.length >= 2 ? parts[1] : '';
        // Display as '[quantity]x [productName]' if available
        const qty = log.before?.quantity;
        const name = log.before?.productName as string | undefined;
        const entityLabel =
            qty && name
                ? `${qty}x ${name}`
                : ((log.before?.deliveryName as string) || name || entityId);
        // Style for button to mimic link appearance
        const linkButtonStyle: React.CSSProperties = {
            background: 'none',
            border: 'none',
            padding: 0,
            color: '#007bff', // Or your theme's link color
            textDecoration: 'underline',
            cursor: 'pointer',
            font: 'inherit',
        };
        return (
          <span>
            {time}: {actionDesc} by {user}{' '}
            {/* Replace <a> with <button> for accessibility */}
            <button
              style={linkButtonStyle}
              onClick={e => { e.preventDefault(); onEntityClick(entityId); }}
              type="button" // Explicitly set type for button
            >
              {entityLabel}
            </button>
          </span>
        );
    };

    // Basic Modal Styling (can be reused/refactored)
    const overlayStyle: React.CSSProperties = {
        position: 'fixed', top: 0, left: 0, right: 0, bottom: 0,
        backgroundColor: 'rgba(0,0,0,0.6)', display: 'flex',
        alignItems: 'center', justifyContent: 'center', zIndex: 40,
    };
    const modalStyle: React.CSSProperties = {
        backgroundColor: 'white', padding: '20px 30px', border: '1px solid #ccc',
        borderRadius: '8px', width: '80%', maxWidth: '800px', maxHeight: '85vh',
        display: 'flex', flexDirection: 'column', boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    };
    const logListStyle: React.CSSProperties = {
        flexGrow: 1, overflowY: 'auto', border: '1px solid #eee', padding: '10px', marginTop: '15px',
    };
    const logItemStyle: React.CSSProperties = { marginBottom: '5px', paddingBottom: '5px', borderBottom: '1px dashed #eee', fontSize: '0.9em' };

    return (
        <div style={overlayStyle} onClick={onClose}>
            <div style={modalStyle} onClick={(e) => e.stopPropagation()}>
                <h3>User Activity Log (Last 100 entries)</h3>
                
                {loading && <div>Loading logs...</div>}
                {error && <div style={{ color: 'red' }}>{error}</div>}
                
                {!loading && !error && (
                    <div style={logListStyle}>
                        {logs.length === 0 && <div>No activity logs found.</div>}
                        {logs.map((log, index) => (
                            <div key={`${log.timestamp}-${index}`} style={logItemStyle}>
                                {renderLogEntry(log)}
                            </div>
                        ))}
                    </div>
                )}

                <div style={{ marginTop: '20px', textAlign: 'right' }}>
                    <button onClick={onClose} style={{ padding: '8px 15px', fontWeight: 'bold' }}>
                        Close
                    </button>
                </div>
            </div>
        </div>
    );
};

export default UserActivityView;
