export const getAvailableActions = (status: ItemStatus, isFlagged?: boolean): ActionDefinition[] => {
  let actions: ActionDefinition[] = [];
  switch (status) {
    case 'Pending Delivery':
      actions = [
        { id: 'Mark as Delivered', label: 'Mark as Delivered', description: 'Update status to Delivered' },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item' },
        { id: 'View Details', label: 'View Details', description: 'View full item details and history' },
        { id: 'Edit Item', label: 'Edit Item', description: 'Edit item details' },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history' },
      ]; break;
    case 'Delivered':
      actions = [
        { id: 'Archive', label: 'Archive', description: 'Archive this item' },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item' },
        { id: 'View Details', label: 'View Details', description: 'View full item details and history' },
        { id: 'Edit Item', label: 'Edit Item', description: 'Edit item details' },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history' },
      ]; break;
    case 'Issue': // Issue-specific actions
      actions = [
        { id: 'Resolve Issue', label: 'Resolve Issue', description: 'Mark the issue as resolved' },
        { id: 'Add Issue Update', label: 'Add Issue Update', description: 'Add an update note to the ongoing issue' },
        { id: 'Archive', label: 'Archive', description: 'Archive this item' },
        { id: 'View Details', label: 'View Details', description: 'View full item details and history' },
        { id: 'Edit Item', label: 'Edit Item', description: 'Edit item details' },
        { id: 'Add Note', label: 'Add Note', description: 'Add a general note to the item history' },
      ]; break;
    case 'Late':
      actions = [
        { id: 'Mark as Delivered', label: 'Mark as Delivered', description: 'Update status to Delivered' },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item' },
        { id: 'View Details', label: 'View Details', description: 'View full item details and history' },
        { id: 'Edit Item', label: 'Edit Item', description: 'Edit item details' },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history' },
      ]; break;
    case 'Archived':
      actions = [{ id: 'View Details', label: 'View Details', description: 'View full item details and history' }]; break;
    default:
      actions = [{ id: 'View Details', label: 'View Details', description: 'View full item details and history' }]; break;
  }
  if (status !== 'Archived') {
    actions.unshift(
      isFlagged
        ? { id: 'Unflag Item', label: 'Unflag', description: 'Remove the flag from this item' }
        : { id: 'Flag Item', label: 'Flag', description: 'Flag this item for attention' }
    );
  }
  return actions;
};

// Simple Action Menu Component
interface ActionMenuProps {
  itemId: string;
  actions: ActionDefinition[];
  onActionSelected: (itemId: string, action: ActionDefinition) => void;
  onClose: () => void;
  anchorEl: null | HTMLElement; // Added anchorEl for MUI Menu
  open: boolean; // Added open state for MUI Menu
}

// Refactored ActionMenu using MUI
export const ActionMenu: React.FC<ActionMenuProps> = ({ 
  itemId, 
  actions, 
  onActionSelected, 
  onClose, 
  anchorEl, 
  open 
}) => {
  // No longer need useRef or useEffect for click-outside, Menu handles it.

  const handleSelectAction = (action: ActionDefinition) => {
    onActionSelected(itemId, action);
    onClose(); // Close the menu after selection
  };

  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={onClose} // MUI handles closing on click away
      MenuListProps={{
        'aria-labelledby': `actions-button-${itemId}`, // Improve accessibility
      }}
    >
      {actions.map(action => (
        <MenuItem 
          key={action.id} 
          onClick={() => handleSelectAction(action)}
        >
          {action.label}
        </MenuItem>
      ))}
    </Menu>
  );
};

