// --- Draggable Header Component ---
interface DraggableHeaderProps {
  col: ColumnConfig;
}

const DraggableHeader: React.FC<DraggableHeaderProps> = ({ col }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: col.id });

  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
    cursor: 'grab',
    opacity: isDragging ? 0.5 : 1, // Dim when dragging
    touchAction: 'none', // Prevent scrolling on touch devices while dragging
    backgroundColor: isDragging ? '#e0e0e0' : undefined // Highlight when dragging (optional, MUI head might style)
  };

  return (
    // Use MUI TableCell as the sortable header element
    <TableCell 
      ref={setNodeRef} 
      component="th" // Specify it's a header cell
      scope="col" 
      style={style} 
      {...attributes} 
      {...listeners}
      sx={{ fontWeight: 'bold', cursor: 'grab' }} // Add grab cursor via sx
    >
      {col.label}
    </TableCell>
  );
};

